import Vue from 'vue'
import Vuex from 'vuex'
import component from './module/component.js'

Vue.use(Vuex)

let state = {
  keepAlive: [],
  sum:0,
  sum2:{
      'init':1,
      'obj':{
          'no':2
      },
    }
};

//mutations里面放的是方法，方法主要用于改变state里的数据

let mutations = {
  setKeepAlive: (state, keepAlive) => {
    state.keepAlive = keepAlive
  },
  addNum:(target,val)=>{
    console.log("addNum",target,val);  
    state.sum += (val && typeof val == 'number'?val:0) 
  },
  add:()=>{
    console.log("add",state.sum); 
    state.sum++;
  },
  saveObj:()=>{
      console.log("no",state.sum2)
      state.sum2.obj.no++;
  },
  //解决数据刷新后丢失
  //目前的问题：数据格式不对会报错，可能只是存在开发当中
  saveState:(target,val)=>{
    for(let item in state){
        if(item !== 'component'){
          state[item] = val[item];
        } 
    }
  }
}

//getters和组件的computed 类似，方便直接生成一些可以直接用的数据。当组装的数据要在多个页面使用时，就可以使用 getters 来做

let getters = {

  keepAlive: state => state.keepAlive,
  filter: state => '第'+state.sum+'次'+state.sum2.obj.no+'回'

}
let actions = {
    setStateChange :({commit},val)=>{
        console.log("val",val)
        commit('addNum',val);
    },
    save:({commit},val)=>{
        commit('saveState',val)
    },
    saveObjAc:({commit})=>{
        commit('saveObj')
    },
}

//vuex 实例化 Vuex.store

const store = new Vuex.Store({
  state,
  mutations: mutations,
  getters: getters,
  actions :actions,
  modules:{
    namespaced: true,
    component,
  }
})

//暴露
export default store;



