 const state = {
    keepAlive: [],
    sum: 20,
    sum2: {
      init: 1,
      obj: {
        no: 2
      }
    }
  }

  //mutations里面放的是方法，方法主要用于改变state里的数据

  const mutations = {
    //解决数据刷新后丢失
    //目前的问题：数据格式不对会报错，可能只是存在开发当中
    addNum2: (state, val) => {
      state.sum += val;
    },
    saceState2: (target, val) => {
      for (const item in state) {
        state[item] = val[item];
      }
    }
  }

  //getters和组件的computed 类似，方便直接生成一些可以直接用的数据。当组装的数据要在多个页面使用时，就可以使用 getters 来做
  const getters = {
    // keepAlive: state => state.keepAlive,
    // filter: state => "第" + state.sum + "次" + state.sum2.obj.no + "回"
  }
  const actions = {
    setStateChange2: ({ commit }, val) => {
      console.log("val", val);
      commit("addNum2", val);
    },
    save2: ({ commit }, val) => {
      commit("saceState2", val);
    },
    saveObjAc2: ({ commit }) => {
      commit("saveObj");
    }
  }
  export default {state, mutations, actions, getters}


