<template>
  <div id="app">
    <v-header></v-header>
    <!-- router view 用于盛放 router link 跳转的组件，若没有则仅跳转，无显示！！！ -->
    <!-- ps：control + command +/  为html注释快捷键 -->
    <div class="content">
      <router-view> </router-view>
    </div>
    <!-- 页面底部 -->
    <div class="bottom-msg">
      <span class="bottom-content">作者：Will</span>
      <span class="bottom-content"> copyright@ Will Zhang 2020</span>
    </div>
  </div>
</template>

<script>
import headerEl from "@/components/header/header";
export default {
  name: "App",
  components: {
    "v-header": headerEl
  },
  mounted() {
    console.log("created");

    //this.$router.push({path:'/index/home'})
  },
  created() {
    if (sessionStorage.getItem("store")) {
      this.$store.dispatch("save", JSON.parse(sessionStorage.getItem("store")));
      // sessionStorage.removeItem('store')
    }
    //console.log(Object.assign({},this.$store.state,JSON.parse(sessionStorage.getItem("store"))),this.$store.state);

    //在页面刷新时将vuex里的信息保存到sessionStorage里

    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });
  }
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* color: #2c3e50; */
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
  flex-direction: column;
  min-height: 100vh;
  font-size: 14px;
  width: 100vw;
  overflow-x: hidden;
  background: url('assets/bg.png') no-repeat ;
  background-size: cover;
}

.content {
  width: 100vw;
  /* height: 100%; */
  flex: 1;
  font-size: 0.6rem;
}
.bottom-msg {
  /* position: absolute; */
  /* bottom: 0; */
  height: 6vh;
  width: 100%;
  background-color: #f4f4f4;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  flex-grow: wrap;
  flex-direction: column;
  font-size: 0.6rem;
}
.bottom-msg .bottom-content {
  line-height: 3vh;
  vertical-align: middle;
}
</style>



