<template>
  <!-- 页面头部 -->
  <div class="header">
    <ul class="header-item">
    <!-- 图标 -->
    <li class="home hover">
      <div class="headerIcon">
        <img :src="icon" />
      </div>
      <!--首页 -->
    </li>
    <li class="home hover">
      <router-link to="/index/home">首页</router-link>
      <!-- 搜索 -->
    </li>
    <li class=" hover user">
      <div class="search-content">
        <input class="search" placeholder="      请输入信息" />
      </div>
      <!-- 设置 -->
    </li>
    <li class=" hover user">
      <router-link to="/welcomeMyPage">个人中心</router-link>
      <!-- 个人中心 -->
    </li>
    <li class="setting hover">
      <router-link to="/index/setting">设置</router-link>
    </li>
  </ul>
  </div>
  
</template>
<script>
export default {
  name: "headerEl",
  data() {
    return {
      icon: "./static/image/icon/apple.png"
    };
  }
};
</script>
<style>
@media screen and (max-width: 768px) {
  .header-item {
    width: 100vw;
  }
}
@media screen and (min-width: 769px )and (max-width: 1199px) {
  .header-item {
    width: 100vw;
  }
}
@media screen and (min-width: 1200px ){
  .header-item {
    width: 1200px;
    margin: 0 auto;
  }
}
.header{
  width: 100vw;
  background-color: #5e616d;
}
input {
  background: none;
  outline: none;
  border: none;
}
.header-item {
  height: 10vh;
  font-size: 0.7rem;
}
.header li {
  vertical-align: middle; /* 此样式应放在子标签上，放在父标签上无效！ */
  font-weight: bold;
}
.home,
.user,
.setting {
  height: 100%;
  display: inline-block;
  line-height: 10vh;
}
.headerIcon {
  height: 10vh;
  padding: 1vh;
  box-sizing: border-box; /* 默认 content-box 导致padding(border也是)计算在height之外 换成border-box则将padding计算在内 */
  -moz-box-sizing: border-box; /* Firefox */
  -webkit-box-sizing: border-box; /* Safari */
}
.header img {
  height: 100%;
  width: 10vh;
}
.header .search-content {
  border: none;
  border-radius: 50px;
  border: 2px solid #fff;
  /* vertical-align: middle; */
  margin: auto;
  box-sizing: border-box;
  line-height: 6vh;
  display: inline-block;
}
.header .search {
  border-radius: 50px;
}
@-webkit-keyframes hover-zoom {
  0% {
    background: #f4f4f4;
  }
  50% {
    background: #f4f4f4;
  }
  100% {
    background: coral;
  }
}

.hover :hover {
  animation: hover-zoom 0.8s;
  background-color: coral;
}
/* 问题情况是：设置li hover 样式，结果出现hover时，只有里面的文字发生变化；
   问题原因是：a:hover 的权重大于li:hover,并且a标签是行内元素；
   改进措施是：将li 中的a 设置为 display：block；
 */
li a {
  display: block;
}
.right {
  float: right;
  position: absolute;
}
.right ::after {
  clear: both;
}
.position1 {
  right: 2%;
}
.position2 {
  right: 7%;
}
.position3 {
  right: 15%;
}
</style>



