<template>
    <div class="bg">
        <button class="btn" @click="getData">增加</button><span>{{ '调用接口次数'+filter }}</span>
        <button class="btn" @click="getData2">增加module</button><span>{{ '调用接口次数'+Sum2 }}</span>
        <div v-for="(item,index) in titleList" :key="index" class="text-style"> <span class="titleFont">{{ Number(index) + 1 }}</span>{{ item.comments }}</div>
        <button class="btn" @click="core1">查看用户信息</button>
    </div>
</template>
<script>
import axios from 'axios'
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
export default {
    name:'welcomeMypage',
    data(){
        return{
            titleList:[],
        }
    },
    created(){
        this.core();
        this.core1();
        //this.getData();
    },
    computed:{
        // ...mapState({
        //     Sum:"sum",
        // }),
        ...mapState({
            Sum2:state => state.component.sum
        }),
        ...mapGetters({
            filter:'filter'
        }),
        test(){
            let i = this.$store.state.component.sum
            console.log("cc",i);
            return i;
        }

    },
    methods:{
        getData(){
            this.$http("/getNews").then(res=>{
                return res
            }).then(res => {
                // this.$store.dispatch('saveObjAc');
                this.$store.dispatch('setStateChange',1);
                this.titleList.push({comments:"First "+res.data.comments});
                return this.$http("/getNews")
            }).then(res=>{
                this.$store.dispatch('setStateChange',1);
                    this.titleList.push({comments:"Second "+res.data.comments});
                    return this.$http("/getNews")
            }).then(res=>{
                this.$store.dispatch('setStateChange',1);
                    this.titleList.push({comments:"Third "+res.data.comments});
            })
        },
        getData2(){
            this.$store.dispatch('setStateChange2',3)
            console.log("2",this.$store.component)
        },
        core(){
            
            axios({url:"https://api.66mz8.com/api/music.163.php",method:"get",responseType:"json"}).then(res=>{
                this.$store.dispatch('setStateChange',1);
                this.titleList.push({comments:"First "+res.data.comments});

            })
        },
        
        core1(){
            
            axios({url:"https://3d0b81ca-8066-4f2e-a935-82006de3bea8.bspapp.com/http/getLoginInfo",method:"post",responseType:"json"}).then(res=>{
                // this.$store.dispatch('setStateChange',1);
                // this.titleList.push({comments:"First "+res.data.comments});
                this.titleList.push({comments:"userInfo: "+res.data.data[0].userName});
                this.titleList.push({comments:"loginId: "+res.data.data[0].loginId});
                this.titleList.push({comments:"password: "+res.data.data[0].password});
                console.log("getInfo",res);
            })
        }
    }
}
</script>
<style lang="less" scoped>
.bg{
    text-align: center;
    .text-style{
    text-align: left;
    .titleFont{
    font-weight: bold;
    margin: 0 10px 10px 0;
}
    }
    .btn{
    padding: 5px;
    margin: 20px auto;
}
}



</style>


