import axios from 'axios'
import router from '@/router'
import Vue from 'vue'
const http = axios.create({
    // baseURL: 'http://localhost:8092/',
    withCredentials: true,
    crossDomain: true,
    timeout: 30000,
})

// request 拦截器
http.interceptors.request.use(
    config => {
        return config
    },
    error => {
        return Promise.reject(error)
    }
)
// respone 拦截器
http.interceptors.response.use(response => {
    let data = response.data
    return Promise.resolve(response)
}, error => {
    return Promise.reject(error)
})
export function buildPath(url, path) {
    if (!path) return url
    return url.replace(placeholder, function($1, $2) {
        return $2 = encodeURIComponent(path[$2])
    })
}

function apiAxios(url, payload, isToast = false,isDataFlag) {
    const req = url;
    const urlPath = payload && payload.path ? buildPath(req.url, payload.path) : req.url
    const params = payload ? payload : null
    return new Promise((resolve, reject) => {
        http({
            url: urlPath,
            method: req.method.toUpperCase(),
            data: (isDataFlag) && payload ? payload : null,
            params: (!isDataFlag) && payload ? payload : null,
        }).then(res => {
            resolve(res)
        }).catch(error => {
            reject(error)
                //true 不展示 falsa 展示
            if (isToast) {
                console.log(error)
            } else {
            }

        })
    })
}

export default {
    install: function(Vue) {
        Vue.prototype.request = (url, payload, isToast,isDataFlag) => apiAxios(url, payload, isToast,isDataFlag)
    },
    $post: (url, payload) => apiAxios(url, payload),
    http,
}



